i {
	font-size: 2rem;
	padding: 5px;
	margin: 5px;
}

button {
	border: none;
	background: none;
	margin: 2px;
	cursor: pointer;
	transition: .2s;
}

button:hover {
	background: #61dafb;
	opacity: .75;
	color: white;
}

